"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Sheet, SheetContent, SheetTrigger } from "@/components/ui/sheet";
import { Badge } from "@/components/ui/badge";
import { Avatar, AvatarFallback } from "@/components/ui/avatar";
import { DropdownMenu, DropdownMenuContent, DropdownMenuItem, DropdownMenuTrigger } from "@/components/ui/dropdown-menu";
import AssistantPopup from "@/components/ai/AssistantPopup";

const navigationItems = [
  { name: "Overview", href: "/dashboard", section: "main" },
  { name: "Inventory", href: "/dashboard/inventory", section: "core" },
  { name: "Sales", href: "/dashboard/sales", section: "core" },
  { name: "Orders", href: "/dashboard/orders", section: "core" },
  { name: "Returns", href: "/dashboard/returns", section: "core" },
  { name: "Analytics", href: "/dashboard/analytics", section: "core" },
  { name: "Invoice Generator", href: "/dashboard/invoice", section: "finance" },
  { name: "Payments", href: "/dashboard/payments", section: "finance" },
  { name: "HR Panel", href: "/dashboard/hr", section: "management" },
  { name: "Finance", href: "/dashboard/finance", section: "management" },
  { name: "Marketing", href: "/dashboard/marketing", section: "management" },
  { name: "Production", href: "/dashboard/production", section: "management" },
  { name: "Projects", href: "/dashboard/projects", section: "management" },
  { name: "Employer Access", href: "/dashboard/employer", section: "access" },
];

const sectionTitles = {
  main: "Dashboard",
  core: "Core Operations",
  finance: "Financial",
  management: "Management",
  access: "Access Control"
};

export default function DashboardLayout({
  children,
}: {
  children: React.ReactNode;
}) {
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);
  const [notifications] = useState(5);
  const [showAIAssistant, setShowAIAssistant] = useState(false);
  const [isVoiceListening, setIsVoiceListening] = useState(false);

  const Sidebar = ({ className = "" }: { className?: string }) => (
    <div className={`bg-white border-r border-gray-200 ${className}`}>
      <div className="p-6 border-b border-gray-200">
        <h2 className="text-xl font-bold text-gray-900">Smart Dashboard</h2>
        <p className="text-sm text-gray-600 mt-1">Excel-like Business Management</p>
      </div>
      
      <nav className="p-4 space-y-6">
        {Object.entries(sectionTitles).map(([section, title]) => (
          <div key={section}>
            <h3 className="text-xs font-semibold text-gray-500 uppercase tracking-wider mb-3">
              {title}
            </h3>
            <div className="space-y-1">
              {navigationItems
                .filter(item => item.section === section)
                .map((item) => (
                  <a
                    key={item.name}
                    href={item.href}
                    className="flex items-center px-3 py-2 text-sm font-medium text-gray-700 rounded-md hover:bg-gray-100 hover:text-gray-900 transition-colors"
                  >
                    {item.name}
                  </a>
                ))}
            </div>
          </div>
        ))}
      </nav>
    </div>
  );

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Desktop Sidebar */}
      <div className="hidden lg:fixed lg:inset-y-0 lg:flex lg:w-64 lg:flex-col">
        <Sidebar />
      </div>

      {/* Mobile Sidebar */}
      <Sheet open={isMobileMenuOpen} onOpenChange={setIsMobileMenuOpen}>
        <SheetContent side="left" className="p-0 w-64">
          <Sidebar />
        </SheetContent>
      </Sheet>

      {/* Main Content */}
      <div className="lg:pl-64">
        {/* Top Header */}
        <header className="bg-white shadow-sm border-b border-gray-200">
          <div className="flex items-center justify-between px-4 py-4">
            <div className="flex items-center space-x-4">
              <Sheet>
                <SheetTrigger asChild>
                  <Button variant="ghost" size="sm" className="lg:hidden">
                    <span className="sr-only">Open sidebar</span>
                    <div className="w-6 h-6 flex flex-col justify-center space-y-1">
                      <div className="w-full h-0.5 bg-gray-600"></div>
                      <div className="w-full h-0.5 bg-gray-600"></div>
                      <div className="w-full h-0.5 bg-gray-600"></div>
                    </div>
                  </Button>
                </SheetTrigger>
                <SheetContent side="left" className="p-0 w-64">
                  <Sidebar />
                </SheetContent>
              </Sheet>
              
              <div>
                <h1 className="text-lg font-semibold text-gray-900">Dashboard</h1>
                <p className="text-sm text-gray-600">Manage your business operations</p>
              </div>
            </div>

            <div className="flex items-center space-x-4">
              {/* AI Assistant Button */}
              <Button 
                variant="outline" 
                size="sm"
                onClick={() => setShowAIAssistant(true)}
              >
                🤖 AI Assistant
              </Button>

              {/* Voice Command Button */}
              <Button 
                variant={isVoiceListening ? "default" : "outline"}
                size="sm"
                onClick={() => {
                  setIsVoiceListening(!isVoiceListening);
                  setShowAIAssistant(true);
                }}
              >
                {isVoiceListening ? "🎤 Listening..." : "🎤 Voice"}
              </Button>

              {/* Notifications */}
              <Button variant="ghost" size="sm" className="relative">
                Alerts
                {notifications > 0 && (
                  <Badge className="absolute -top-1 -right-1 h-5 w-5 rounded-full p-0 text-xs">
                    {notifications}
                  </Badge>
                )}
              </Button>

              {/* User Menu */}
              <DropdownMenu>
                <DropdownMenuTrigger asChild>
                  <Button variant="ghost" className="relative h-8 w-8 rounded-full">
                    <Avatar className="h-8 w-8">
                      <AvatarFallback>AD</AvatarFallback>
                    </Avatar>
                  </Button>
                </DropdownMenuTrigger>
                <DropdownMenuContent className="w-56" align="end">
                  <DropdownMenuItem>Profile Settings</DropdownMenuItem>
                  <DropdownMenuItem>Export Data</DropdownMenuItem>
                  <DropdownMenuItem>Email Summary</DropdownMenuItem>
                  <DropdownMenuItem>Backup to Gmail</DropdownMenuItem>
                  <DropdownMenuItem>Sign Out</DropdownMenuItem>
                </DropdownMenuContent>
              </DropdownMenu>
            </div>
          </div>
        </header>

        {/* Page Content */}
        <main className="p-6">
          {children}
        </main>
      </div>

      {/* AI Assistant Popup */}
      <AssistantPopup 
        isOpen={showAIAssistant} 
        onClose={() => {
          setShowAIAssistant(false);
          setIsVoiceListening(false);
        }} 
      />
    </div>
  );
}
